import { forwardRef } from 'react';
import { styled } from '../../utils';
import { TwBaseProps, FormattedColor, TwStyleSystemProps, Size } from '../../types';
import {
  Timeline as MantineTimeline,
  TimelineProps as MantineTimelineProps,
  TimelineItemProps as MantineTimelineItemProps,
  DefaultProps,
} from '@mantine/core';
import { PropsFrom } from '../../utility-types';
import { Skeleton } from '../Skeleton/Skeleton';
import { Box } from '../Box/Box';

export type TimelineBulletVariant = 'colored' | 'transparent';

interface ITimeline extends React.FC<TimelineProps> {
  Item: React.ForwardRefExoticComponent<PropsFrom<typeof MantineTimeline.Item> & TimelineItemProps>;
}

export interface TimelineItemProps
  extends TwBaseProps,
  TwStyleSystemProps,
  Omit<MantineTimelineItemProps, keyof DefaultProps> {
  color?: FormattedColor;
}

export interface TimelineProps extends TwBaseProps, Omit<MantineTimelineProps, keyof DefaultProps> {
  color?: FormattedColor;
  radius?: Size;
  bulletVariant?: TimelineBulletVariant;
  loading?: boolean;
  skeletonLines?: number;
}

const Root = styled(MantineTimeline)<MantineTimelineProps>(() => ({}));

const Timeline: ITimeline = (props) => {
  const { bulletVariant = 'colored', loading = false, skeletonLines = 5, ...rest } = props;
  const borderColor = bulletVariant === 'transparent' || loading ? 'transparent' : props.color;
  const backgroundColor = bulletVariant === 'transparent' || loading ? 'transparent' : props.color;

  return (
    <Root
      {...rest}
      styles={() => {
        return {
          itemBullet: {
            borderColor,
            backgroundColor,
            '&[data-active]': {
              borderColor,
              backgroundColor
            },
            '&[data-with-child]': {
              borderColor,
              backgroundColor
            }
          },
        };
      }}
    >
      {loading ? Array.from(Array(skeletonLines), (_, index) => {
        return (
          <MantineTimeline.Item
            key={index}
            title={<Skeleton width="100%" height={20} />}
            bullet={<Skeleton circle width="100%" height="100%" />}
          >
            <Box mt="md">
              <Skeleton width="80%" height={25} />
            </Box>
          </MantineTimeline.Item>
        );
      }) : props.children}
    </Root>
  );
};

Timeline.Item = forwardRef((props: TimelineItemProps, ref) => {
  return <MantineTimeline.Item {...props} ref={ref} />;
});

export { Timeline };
